html {
	overflow: auto !important; }

.header-page {
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 6px 0;
		background-color: #dddddd; }

	&__center {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center; }

	&__descr {
		width: 40%;
		@include academy-font;
		font-size: 18px;
		color: #555555;
		text-transform: uppercase;
		padding-top: 45px;
		padding-bottom: 50px; }

	&__logo {
		position: relative;
		width: 20%;
		top: -12px;
		align-self: flex-start;

		&::before {
			content: '';
			position: absolute;
			top: 130px;
			left: 100%;
			width: calc(940px / 2 - 88px);
			height: 30px; }

		&::after {
			content: '';
			position: absolute;
			top: 130px;
			right: 100%;
			width: calc(940px / 2 - 88px);
			height: 30px; } }

	&__title {
		position: relative;
		width: 20%;
		top: -12px;
		margin: 0 auto;
		align-self: flex-start;

		&::before {
			content: '';
			position: absolute;
			top: 3px;
			left: 100%;
			width: calc(940px / 4 - 88px);
			height: 30px; }

		&::after {
			content: '';
			position: absolute;
			top: 3px;
			right: 100%;
			width: calc(940px / 4 - 88px);
			height: 30px; } }


	&__social {
		width: 40%;
		margin-right: 1px;
		padding-top: 45px;
		padding-bottom: 50px; }

	&__nav {
		margin-bottom: 46px; }

	&__menu {
		justify-content: center; } }

.decor-title {
	&::before {
		background-image: url(/img/ico/arrow-decor-right.jpg),url(../img/ico/pattern.png);
		background-repeat: no-repeat,repeat-x;
		background-size: auto,1px;
		background-position: left,center; }

	&::after {
		background-image: url(/img/ico/arrow-decor-left.jpg),url(../img/ico/pattern.png);
		background-repeat: no-repeat,repeat-x;
		background-size: auto,1px;
		background-position: right,center; } }

.main-title {
	@include academy-font;
	font-size: 32px;
	font-weight: normal;
	color: #000000;
	text-align: center;
	text-transform: uppercase;
	padding-bottom: 50px; }


.main-menu {
	display: flex;

	&__item:last-child {
		.main-menu__link::before {
			display: none; } }

	&__link {
		position: relative;
		display: block;
		padding: 0 10px;
		line-height: 1.2;
		color: #000000;
		text-decoration: none;
		text-transform: uppercase;
		transition: color .2s;

		&::before {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			width: 1px;
			height: 12px;
			background-color: #000000; }

		&:hover {
			text-decoration: underline; }

		&--category {
			font-size: 14px; } } }


.social-list {
	display: flex;
	justify-content: flex-end;
	flex-wrap: wrap;

	&__item {
		margin-right: 19px;

		&:last-child {
			margin-right: 0; } }

	&__btn {
		position: relative;
		display: block;
		width: 23px;
		height: 22px;
		background-position: 50%;
		background-repeat: no-repeat;

		&--pin {
			background-image: url(../img/ico/pin.png); }

		&--fb {
			background-image: url(../img/ico/fb.png); }

		&--insta {
			background-image: url(../img/ico/insta.png); } } }

.logo {
	display: block; }

.product-sect {
	padding-bottom: 92px; }

.product {
	display: flex;

	&__slider {
		width: 32%; }

	&__descr {
		width: 68%;
		padding-left: 112px; }

	&__img-wrap {
		position: relative;
		display: block;
		margin-bottom: 30px;
		padding-bottom: 92%; }

	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover; }

	&__name {
		margin-bottom: 10px;
		font-size: 20px;
		font-weight: normal;
		color: #000000;
		text-decoration: none; }

	&__code {
		margin-bottom: 10px; }

	&__price-wrap {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;
		padding: 16px 0;
		border-top: 1px solid #dddddd;
		border-bottom: 1px solid #dddddd; }

	&__price {
		font-size: 20px;
		color: #777777; } }

.product-container {
	padding-left: 5px;
	padding-right: 10px; }

.btn-buy {
	display: block;
	text-decoration: none;
	font-size: 20px;
	color: #8c0000;
	background: transparent; }

.thumbs {
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 30px; }

	&__item {
		width: 25%;
		padding: 9px; }

	&__link {
		position: relative;
		display: block;
		padding-bottom: 100%;
		border: 2px solid #ebebeb; }

	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover; } }

// .mfp-container

.mfp-wrap {
	width: 940px;
	height: 580px;
	margin: 0 auto;
	left: 50%;
	top: 0;
	overflow: hidden !important;
	transform: translate(-50%, 0);
	background: #a8771c54; }

.mfp-bg {
	display: none; }

.mfp-bg, .mfp-wrap {
	position: absolute;
	top: 325px; }

.mfp-container {
	background: #fff;
	border: 3px solid #dddddd; }

.img.mfp-img {
	padding: 0 !important; }

