  overflow: auto !important;
}

.header-page__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 6px 0;
  background-color: #dddddd;
}

.header-page__center {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-page__descr {
  width: 40%;
  font-family: "academy-engraved", serif;
  font-size: 18px;
  color: #555555;
  text-transform: uppercase;
  padding-top: 45px;
  padding-bottom: 50px;
}

.header-page__logo {
  position: relative;
  width: 20%;
  top: -12px;
  align-self: flex-start;
}

.header-page__logo::before {
  content: '';
  position: absolute;
  top: 130px;
  left: 100%;
  width: calc(940px / 2 - 88px);
  height: 30px;
}

.header-page__logo::after {
  content: '';
  position: absolute;
  top: 130px;
  right: 100%;
  width: calc(940px / 2 - 88px);
  height: 30px;
}

.header-page__title {
  position: relative;
  width: 20%;
  top: -12px;
  margin: 0 auto;
  align-self: flex-start;
}

.header-page__title::before {
  content: '';
  position: absolute;
  top: 3px;
  left: 100%;
  width: calc(940px / 4 - 88px);
  height: 30px;
}

.header-page__title::after {
  content: '';
  position: absolute;
  top: 3px;
  right: 100%;
  width: calc(940px / 4 - 88px);
  height: 30px;
}

.header-page__social {
